export default class WDU {
    /**
     * çææéè¦çHTMLåç´ 
     * 
     * @param {Array} elements elementsåç´ ä¸­ï¼ç¬¬ä¸é¡¹-éè¦çæçhtmlåç´ çåç§°ï¼ç¬¬äºé¡¹-çæçhtmlåç´ çç±»å
     */
    genHTML(elements) {
        let BOXES = {}

        elements.forEach(element => {
            const boxName = element[1]
            const ele = document.createElement(element[0])
            const className = element[1]
            ele.setAttribute('class', `${this.PREFIX}-${className}`)
            BOXES[boxName] = ele
        })

        return BOXES
    }

    /**
     * åå®ä¾åç´ çåå§åæ¹æ³
     * 
     * @param prefix å½ååç´ ç±»å, 
     * @param exe éæ§è¡çæ¹æ³
     */
    init(prefix, exe) {
        const all = Array.from(document.querySelectorAll(`.${prefix}`))
        if(all){
            all.forEach(one => {
                exe(one)
            })
        }
    }

    /**
     * å¤å®ä¾åç´ çåå§åæ¹æ³
     * 
     * @param prefix å½ååç´ ç±»å 
     * @param single åä¾ç»ä»¶ç±»
     */
    initMult(prefix, single) {
        const all = Array.from(document.querySelectorAll(`.${prefix}`))
        all.forEach(one => {
            new single(one)
        })
    }

    /**
     * æ¦é¤æ ç­¾ä¸çéç½®é¡¹
     * 
     * @param ele è¦æ¦é¤éç½®çåç´ 
     */
    wipeOption(ele) {
        if (ele.dataset) {
            // å° DOMstringï¼è½¬ä¸º Object
            const keys = Object.keys(Object.assign({}, ele.dataset))

            keys.forEach(key => {
                ele.removeAttribute(`data-${key}`)
            })
        }
    }

    /**
     * åªå Element ç±»åçåç´ 
     * @param element ä¼ å¥ç node éå
     */
    getElementChilds(element){
        return Array.from(element.childNodes).filter((item) => {
            return item.nodeType == 1
        })
    }

    /**
     * ç¦ç¨æ´ä¸ªç»ä»¶
     * 
     * @param ele ç»ä»¶åç´ 
     */
    disableComponent(ele, prefix){
        const childs = Array.from(ele.querySelectorAll(`.${prefix} *`))
        childs.forEach(item => {
            item.addEventListener('click', e => {
                e.stopPropagation()
            })
            item.style['cursor'] = 'not-allowed'
        })
    }
}